import React, { Component } from "react";
import Card from "../../../../../_COMPONENTS/Common/SectionCards/card";

export default class ServicesSectionCard extends Component {
  state = {
    data: [
      {
        Name: [
          {
            Header: "Services",
            Path: "services",
            Direction: "INBOUND",
            TLD: "",
          },
        ],
        Data: [
          {
            Title: "COMING SOON...",
            Path: "",
            Direction: "",
            TLD: "",
          },
          {
            Title: "",
            Path: "",
            Direction: "INBOUND",
            TLD: "",
          },
        ],
        Info: [{ LastUpdate: " Last updated 3 mins ago " }],
      },
    ],
  };
  render() {
    const { Name, Data, Info } = this.state.data[0];
    const { Header, Path, Direction, TLD } = Name[0];
    const { LastUpdate } = Info[0];
    return (
      <>
        <Card
          Title={Header}
          Data={Data}
          Path={Path}
          Direction={Direction}
          TLD={TLD}
          LastUpdate={LastUpdate}
        />
      </>
    );
  }
}
