import React, { Component } from "react";
import Card from "../../../../../_COMPONENTS/Common/SectionCards/card";

export default class CompanySectionCard extends Component {
  state = {
    data: [
      {
        Name: [
          {
            Header: "Company",
            Path: "company",
            Direction: "INBOUND",
            TLD: "",
          },
        ],
        Data: [
          {
            Title: "About",
            Path: "about",
            Direction: "INBOUND",
            TLD: ".com",
          },
          {
            Title: "Contact Us",
            Path: "construction",
            Direction: "INBOUND",
            TLD: "",
          },
          {
            Title: "Projects",
            Path: "construction",
            Direction: "INBOUND",
            TLD: "",
          },
          {
            Title: "Careers",
            Path: "construction",
            Direction: "INBOUND",
            TLD: "",
          },
        ],
        Info: [{ LastUpdate: " Last updated 3 mins ago " }],
      },
    ],
  };
  render() {
    const { Name, Data, Info } = this.state.data[0];
    const { Header, Path, Direction, TLD } = Name[0];
    const { LastUpdate } = Info[0];
    return (
      <>
        <Card
          Title={Header}
          Data={Data}
          Path={Path}
          Direction={Direction}
          TLD={TLD}
          LastUpdate={LastUpdate}
        />
      </>
    );
  }
}
