import React, { Component } from "react";
import Card from "../../../../../_COMPONENTS/Common/SectionCards/card";

export default class ProductsSectionCard extends Component {
  state = {
    data: [
      {
        Name: [
          {
            Header: "Products",
            Path: "products",
            Direction: "INBOUND",
            TLD: ".com",
          },
        ],
        Data: [
          {
            Title: "ElementX",
            Path: "elementx",
            Direction: "INBOUND",
            TLD: ".com",
          },
          {
            Title: "SparkX",
            Path: "sparkx",
            Direction: "INBOUND",
            TLD: ".com",
          },
          {
            Title: "ClipX",
            Path: "clipx",
            Direction: "INBOUND",
            TLD: ".com",
          },
        ],
        Info: [{ LastUpdate: " Last updated 3 mins ago " }],
      },
    ],
  };
  render() {
    const { Name, Data, Info } = this.state.data[0];
    const { Header, Path, Direction, TLD } = Name[0];
    const { LastUpdate } = Info[0];
    return (
      <>
        <Card
          Title={Header}
          Data={Data}
          Path={Path}
          Direction={Direction}
          TLD={TLD}
          LastUpdate={LastUpdate}
        />
      </>
    );
  }
}
